import firebase from "firebase/compat";
import {useCallback, useEffect, useMemo, useState} from "react";
import {getStorage, ref} from "firebase/storage";
import styled from "@emotion/styled";
import {uploadBytesResumable} from "@firebase/storage";
import {getDatabase, ref as DbRef, update} from "firebase/database";
import {Spinner} from "react-bootstrap";
import Button from 'react-bootstrap/Button';

export const UploadPage = (props) => {
  const [uploadError, setUploadError] = useState();
  const [fileError, setFileError] = useState();
  const [success, setSuccess] = useState();
  const [uploading, setUploading] = useState(false);
  const [fileName, setFileName] = useState();

  const storage = useMemo(() => getStorage(), []);

  const [uploadEnabled, setUploadEnabled ] = useState(false);

  useEffect(() => {
    if (fileName && !isVideo(fileName)) {
      setFileError("Please choose a video file");
      setUploadEnabled(false);
    } else if (fileName) {
      setUploadEnabled(true);
      setFileError(undefined);
    }
  }, [fileName]);

  const onUpload = useCallback(() => {
    setUploadError(undefined);
    setSuccess(undefined);
    const files = document.querySelector('#upload').files;
    if (files.length > 0) {
      const file = files[0];
      const name = file.name;
      const metadata = {contentType: file.type};
      const storageRef = ref(storage, 'upload/' + props.user.uid + '/' + name);

      setUploading(true);

      const task = uploadBytesResumable(storageRef, file, metadata);

      update(DbRef(getDatabase(), 'users/' + props.user.uid), {email: props.user.email, name: props.user.displayName})
        .then(() => console.log('Updated successfully'))
        .catch(() => console.log('Could not update user'));


      task.on('state_changed', snapshot => {
        },
        err => {
          console.log('Could not upload the file', err);
          switch (err.code) {
            case 'storage/unauthorized':
              setUploadError('Could not upload the file: permission denied');
              break;
            case 'storage/unauthenticated':
              setUploadError('Could not upload the file, please sign in');
              break;
            case 'storage/quota-exceeded':
              setUploadError("Could not upload the file, please try again later");
              break;
            case 'storage/unknown':
              setUploadError(err.message);
              break;
            default:
              setUploadError("Could not upload the file, please try again later");
              break;
          }
          setSuccess(false);
          setUploading(false);
        },
        () => {
          console.log('Uploaded the file: ' + name);
          setSuccess(true);
          setUploading(false);
        });
    }

  }, [props.user]);

  const onChangeFile = () => {
    const file = document.querySelector('#upload').files[0];
    if (file) {
      setFileName(file.name);
    }
  }

  return (
    <UploadForm>
      <Header>
        <Title>{props.user.displayName}</Title>
        <SignOutButton onClick={() => void firebase.auth().signOut()}>Sign Out</SignOutButton>
      </Header>
      {success && <div>
        <SuccessMessagePrimary>Video has been successfully uploaded.</SuccessMessagePrimary>
        <SuccessMessage>We will send the double Ace Trace back to you soon!</SuccessMessage>
      </div>
      }
      {!success && <FormContainer>
        <FileChooser>
          <input type="file" onChange={onChangeFile} id="upload" name="file" style={{display: 'none'}}/>
          <FileChooserButton enabled={!uploading}>Choose video</FileChooserButton>
          <FileName>{fileName}</FileName>
        </FileChooser>
        {fileError && <ErrorMessage>{fileError}</ErrorMessage>}

        <Label>We need your email to send the Double Ace Trace video back</Label>
        <EmailInput className="input is-medium" type="text" name="email" placeholder="Email" required
                    defaultValue={props.user.email}/>
        <UploadButton disabled={uploading || !uploadEnabled} onClick={() => uploadEnabled && onUpload()}>
          {uploading && <Spinner as="span" animation="border" size="sm" role="status" aria-hidden="true"/>}
          {uploading ? 'Uploading...' : 'Upload'}
        </UploadButton>
        {uploadError && <ErrorMessage>{uploadError}</ErrorMessage>}
      </FormContainer>
      }
    </UploadForm>
  );
};

function getExtension(filename) {
  const parts = filename.split('.');
  return parts[parts.length - 1];
}

function isVideo(filename) {
  const ext = getExtension(filename);
  switch (ext.toLowerCase()) {
    case 'm4v':
    case 'avi':
    case 'mpg':
    case 'mp4':
    case 'mov':
      return true;
    default:
      return false;
  }
}

const Header = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
`

const UploadForm = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 500px;
  padding-right: 10px;
  align-items: center;
  text-align: left;
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const Title = styled.div`
  font-weight: 700;
  font-size: 1.5rem;
  color: #FF8F48;
  margin-right: 40px;
`;

const FileChooser = styled.label`
  outline: none;
  text-align: left;
  margin: 10px;

  display: flex;
  align-items: center;
`

const FileChooserButton = styled.div`
  outline: none;
  border-color: #FFCE5A;
  background: ${props => props.enabled ? 'linear-gradient(45deg, #FFCE5A, #FF8F48);' : '#777;'}
  color: #fff;
  transition: all 0.5s;

  border-radius: 500px;
  cursor: ${props => props.enabled ? 'pointer;' : 'not-allowed;'}

  font-size: 1rem;
  font-weight: 600;
  padding: 8px 16px 8px 16px;
  margin-right: 10px;
`

const FileName = styled.div`
  width: 130px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const UploadButton = styled(Button)`
  outline: none;
  border-color: #FFCE5A;
  background: linear-gradient(45deg, #FFCE5A, #FF8F48);
  color: #fff;
  transition: all 0.5s;

  border-radius: 500px;

  font-size: 1rem;
  font-weight: 600;
  padding: 8px 16px 8px 16px;
  margin: 10px;
  &:disabled {
    background: #777;
  }
  
  > span {
    padding-right: 8px;
  } 
`

const SignOutButton = styled(Button)`
  outline: none;
  background: transparent;
  color: #fff;
  transition: all 0.5s;

  border-radius: 500px;
  border: 2px solid #999;
  cursor: pointer;

  font-size: 1rem;
  font-weight: 600;
`

const EmailInput = styled.input`
  color: #878787;
  box-shadow: none !important;
  transition: all 0.8s;
  padding-bottom: 3px;
  background-color: #584D68;
  border-color: #423751;
  color: #dbdbdb;
  box-shadow: inset 0 1px 2px rgb(10 10 10 / 10%);
  width: 300px;

  -webkit-appearance: none;
  align-items: center;
  border: 1;
  -moz-appearance: none;
  -webkit-appearance: none;
  align-items: center;
  border: 2px solid transparent;
  border-radius: 10px;
  box-shadow: none;
  display: inline-flex;
  height: 2.25em;
  justify-content: flex-start;
  line-height: 1.5;
  padding: 5px;
  position: relative;
  vertical-align: top;
  margin: 10px;

  // States
  &:focus,
  &.is-focused,
  &:active,
  &.is-active {
    outline: none;
  }
  &[disabled] {
    cursor: not-allowed;
   }
`;


const Label = styled.p`
  margin-top: 24px;
`;

const ErrorMessage = styled.p`
  color: #cf8162;
  text-align: left;
`

const SuccessMessagePrimary = styled.div`
  color: #FFCE5A;
  font-size: 22px;
  text-align: center;
`;

const SuccessMessage = styled.div`
  font-size: 18px;
`;
