import 'firebaseui/dist/firebaseui.css'
import {useCallback, useEffect, useState} from "react";
import {UploadPage} from "./UploadPage";
import styled from "@emotion/styled";
import logo from'./logo.svg';
import 'bootstrap/dist/css/bootstrap.min.css';

const firebaseConfig = {
  apiKey: "AIzaSyC8WLfWe86v7z6kGwrQFHgVNfeaMD-7Teo",
  authDomain: "followflight-f0c00.firebaseapp.com",
  databaseURL: "https://followflight-f0c00-default-rtdb.firebaseio.com",
  projectId: "followflight-f0c00",
  storageBucket: "followflight-f0c00.appspot.com",
  messagingSenderId: "364337264488",
  appId: "1:364337264488:web:2230e274aba6b5f41fc8dd",
  measurementId: "G-0TBHKW45Z5"
};

const uiConfig = {
  signInSuccessUrl: '/upload',
  signInOptions: [
    firebase.auth.GoogleAuthProvider.PROVIDER_ID,
    firebase.auth.FacebookAuthProvider.PROVIDER_ID
  ],
  tosUrl: '/terms',
  privacyPolicyUrl: '/privacy'
};

export const app = firebase.initializeApp(firebaseConfig);
const analytics = getAnalytics(app);


const ui = new firebaseui.auth.AuthUI(firebase.auth())

function App() {
  const [user, setUser] = useState();
  const [signing, setSigning] = useState(false);
  const [error, setError] = useState();

  const signIn = useCallback(() => {
    setSigning(true);
    ui.start('#firebaseui-auth-container', uiConfig);
  }, [setSigning]);

  useEffect(() => {
    firebase.auth().onAuthStateChanged(user => {
      setUser(user);
    }, err => setError(err.message));
  }, [setUser, setError])

  useEffect(() => {
    if (ui.isPendingRedirect()) {
      signIn();
    }
  }, [signIn]);

  if (!!user && !error) {
    return (
      <Page>
        <UploadPage user={user}/>
      </Page>
    );
  }

    return (
      <Page>
        <div id='firebaseui-auth-container'/>
        {(!user && !signing || error) &&
          <>
            <p>Please, sign in to send the video</p>
            <Button onClick={signIn}>Sign In</Button>
            <p>{error}</p>
          </>
        }
      </Page>
    );
}

const Page = (props) => {
  return (
    <PageWrapper>
      <a href='/' >
        <img src={logo} alt="" width="112" height="28" />
      </a>
      <ContentWrapper>
        {props.children}
      </ContentWrapper>
    </PageWrapper>
  );
};

const PageWrapper = styled.div`
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  height: 100vh;  
  background-color: #2c2536;
  color: #fff;
  padding: 12px;
`;

const ContentWrapper = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 12px;
  text-align: center;
`;

const Button = styled.a`
  outline: none;
  border-color: #FFCE5A;
  background: linear-gradient(45deg, #FFCE5A, #FF8F48);
  color: #fff;
  transition: all 0.5s;
  
  border-radius: 500px;
  cursor: pointer;
  
  font-size: 1rem;
  font-weight: 600;
  padding: 8px 12px 8px 12px;
  width: 120px;
  margin: 10px;
`

export default App;
